import Action from './action';

class AddEncoding extends Action {
    constructor(spec) {
        super(spec);
        this.channel = spec.channel;
        this.field = spec.field
        if ('style' in spec) { this.style = spec.style }
        else {
            this.style = { "delay": 0, "duration": 0 }
        }
    }

    operate(vis) {
        vis.chart().addEncoding(this.channel, this.field, this.style);
    }
}

export default AddEncoding;