import React from 'react';
import ChartType from '../constant/ChartType';
import getSupportedChartTypes from '@/tool/getSupportedChartTypes';
import Chart from '../Chart';
import { isValid, customizeFact } from '@/view/FactEditor/helper';
import _ from 'lodash';

export const facts2unitVises = (facts, shema, chartDiversity = 0) => {
    for (let i = 0; i < facts.length; i++) {
        let supportedChartTypes = getSupportedChartTypes(shema, facts[i]);
        let choiceCount = parseInt((supportedChartTypes.length + 1) * chartDiversity);
        let choicedChartTypes = supportedChartTypes.slice(0, choiceCount + 1);
        facts[i].chart = choicedChartTypes[Math.floor(Math.random() * choicedChartTypes.length)].chart;
    }
    return facts
}
export const getChoice = (length) => {
    let choice = Math.round(Math.random() * (length - 1));
    return choice;
}


export const getFactChartType = (shema, fact, choice = 0) => {
    let supportedChartTypes = getSupportedChartTypes(fact, shema);
    if (supportedChartTypes.length === 0) return null;
    return supportedChartTypes[getChoice(supportedChartTypes.length)].chart;
}

export const fact2unitSpec = function(filename,specData, fact, data, size, width_height, compound = false) {
    if (!fact.chart || fact.chart === "") {
        fact.chart = getFactChartType(specData.schema, fact); //chart
    }

    let chart = fact.chart;

    if (chart === ChartType.ISOTYPE_BAR_CHART) {
        fact.chart = ChartType.VERTICAL_BAR_CHART;
    }
    fact = customizeFact(fact); //important
    
    /* if (!fact.chart || !isValid(fact)) {
        console.log("no valid", fact)
        return null;
    } */

    let specFact = {
        type:fact.type,
        subspace:fact.subspace,
        measure:fact.measure.map(d => {
            return {
                "field": d.field,
                "aggregate":d.aggregate
            }
        }),
        breakdown: fact.groupby.map(d => {
            return {
                "field": d
            }
        }),
        focus:fact.focus,
    }
    let specChart
    if (width_height) {
        specChart = {
            width: width_height.width,
            height: width_height.height,
            type: "unitvis",
            style: "business",
            duration: 0,
            // showSuggestion: true, //Unsupported
        }
    } else {
        specChart = {
            size: size,
            type: "unitvis",
            style: "business",
            duration: 0,
            // showSuggestion: true, //Unsupported
        }
    }

    specData = {
        ...specData,
        values: data,
    }
    let specNew = {
        data: specData,
        fact: specFact,
        chart: specChart,
    }
    return specNew

}

export const fact2unitVis = function(specData, uuid, fact, data, size, width_height, compound = false) {
    if (!fact.chart || fact.chart === "") {
        fact.chart = getFactChartType(specData.schema, fact); //chart
    }

    let chart = fact.chart;

    if (chart === ChartType.ISOTYPE_BAR_CHART) {
        fact.chart = ChartType.VERTICAL_BAR_CHART;
    }
    fact = customizeFact(fact); //important
    if (!fact.chart || !isValid(fact)) {
        console.log("no valid", fact)
        return null;
    }
    let specFact = {
        ...fact,
        type: fact.type,
        breakdown: fact.groupby.map(d => {
            return {
                "field": d
            }
        })
    }
    let specChart
    if (width_height) {
        specChart = {
            width: width_height.width,
            height: width_height.height,
            type: "unitvis",
            style: "business",
            duration: 0,
            showSuggestion: true, //Unsupported
        }
    } else {
        specChart = {
            size: size,
            type: "unitvis",
            style: "business",
            duration: 0,
            showSuggestion: true, //Unsupported
        }
    }

    specData = {
        ...specData,
        values: data
    }

    let specNew = {
        data: specData,
        fact: specFact,
        chart: specChart,
    }
    /* specData.schema.forEach(a => {
        console.log("{"+
        "\"field\":\""+a.field+
        "\",type\":\""+a.type+
        "\",subtype\":\""+a.subtype+"\""
        "},")
    }); */
    


    return <Chart spec = { _.cloneDeep(specNew) } uuid = { uuid }/>
}