import Titler from './titler'
import Color from '../../visualization/color';

class Fade extends Titler {
    maketitle(chart, style) {
        let svg = chart.svg();
            let words = style.text.split(" ").filter(d => d !== "");
        
            let virtualE = svg.append("text")
                .attr("font-family", 'Arial-Regular')
                .attr("font-size", 24)
                .attr("fill", () => {
                if ("color" in style) {
                        return style["color"];
                    } else {
                        return Color().TEXT;
                    }
                })

                .text(words[0]);

            let textE = svg.append("text")
                .attr("x","50%")
                .attr("y", -10)
                .attr("dominant-baseline", "central")
                // .attr("transform", "translate(" + 50 + "," + (-10) + ")")
                .attr("transform", "translate(" + 375 + "," + (0) + ")")
                .attr("font-family", 'Arial-Regular')
                .attr("font-weight","bold")
                .attr("font-size", 20)
                .attr("fill", () => {
                    if ("color" in style) {
                        return style["color"];
                    } else {
                        return Color().TEXT;
                    }
                })
                .attr("text-anchor", "middle");
            
            let maxWidth = Math.max(virtualE.node().getComputedTextLength(), 720);
            const lineHeight = virtualE.node().getBBox().height * 0.9;

            let line = '';
            let rowCount = 0;
            const maxRow = 4;

            //  duration
            if(style.duration===-1){
                for (let n = 0; n < words.length; n++) {
                    let testLine = line + ' ' + words[n];
                    /*  Add line in testElement */
                    if(rowCount === maxRow - 1){
                        virtualE.text(testLine+ "");
                    }else{
                        virtualE.text(testLine);
                    }
                    /* Messure textElement */
                    let testWidth = virtualE.node().getComputedTextLength();
                    if (testWidth > maxWidth) {
                        if(rowCount === maxRow - 1){//
                            line += "";
                            break;
                        }else{//new row
                            textE.append("tspan")
                                .attr("x", 0)
                                .attr("dy", lineHeight)
                                .text(line)
                                .attr("fill-opacity", 0)
                                .transition()
                                .duration(2000)
                                .attr("fill-opacity", 1);
                            line = words[n];
                            rowCount ++;
                        }
                    } else {
                        line = testLine;
                    }
                }
                
                textE.append("tspan")
                    .attr("x", 0)
                    .attr("dy", lineHeight)
                    .attr("fill-opacity", 0)
                    .text(line)
                    .transition()
                    .delay(style.delay)
                    .duration(2000)
                    .attr("fill-opacity", 1);
                virtualE.remove();
            }
            //  duration
            else{
                for (let n = 0; n < words.length; n++) {
                    let testLine = line + ' ' + words[n];
                    /*  Add line in testElement */
                    if(rowCount === maxRow - 1){
                        virtualE.text(testLine+ "");
                    }else{
                        virtualE.text(testLine);
                    }
                    /* Messure textElement */
                    let testWidth = virtualE.node().getComputedTextLength();
                    if (testWidth > maxWidth) {
                        if(rowCount === maxRow - 1){//
                            line += "";
                            break;
                        }else{//new row
                            textE.append("tspan")
                                .attr("x", 0)
                                .attr("dy", lineHeight)
                                .attr("fill-opacity", 0)
                                .text(line)
                                .transition()
                                .delay(style.delay)
                                .duration(style.duration*0.25)
                                .attr("fill-opacity", 1)
                                .transition()
                                .delay(style.duration*0.5)
                                .duration(style.duration*0.25)
                                .attr("fill-opacity", 0)
                                .remove();
                            line = words[n];
                            rowCount ++;
                        }
                    } else {
                        line = testLine;
                    }
                }
                
                textE.append("tspan")
                    .attr("x", 0)
                    .attr("dy", lineHeight)
                    .attr("fill-opacity", 0)
                    .text(line)
                    .transition()
                    .delay(style.delay)
                    .duration(style.duration*0.25)
                    .attr("fill-opacity", 1)
                    .transition()
                    .delay(style.duration*0.5)
                    .duration(style.duration*0.25)
                    .attr("fill-opacity", 0)
                    .remove();
                virtualE.remove();
                
            }
    }
}

export default Fade