import ActionType from '../action/type';
import _ from 'lodash';
import { fact2unitVis, fact2unitSpec } from '@/tool/fact2unitVis';


const initialState = {
    cardItems: [],
    videoIndex: 0,
    currentSpec: {},
    nowVideoTime: 0,
    allVideoTime: 0,
    isPlayVideo: false,
    leftContent: ActionType.SET_DEFAULT,
    barWidthList: [],
    dragDirection: 'null',
    isMouseDown: false,
    isDrag: false,
    currentX: 0,
    leftOffsetWidth: 0,
    rightOffsetWidth: 0,
    currentBarIndex: -1,
    scenePosition:0,
    sceneScale:1,
    dragCursor:false,
    question:'',
    narrative_spec: '',
    isGenerating: false,
    temp_spec:'',
    operatorType:false,
    animationType:false,
    keys:[],
    operatorAdds:-1
};

export default (state = initialState, action) => {
    const newState = Object.assign({}, state);
    let newFacts;
    // 4action
    switch (action.type) {

        //init video slice card
        case ActionType.INIT_VIDOE_CARD:
            let cards = [];
            let firstCard = {};
            action.facts.map((fact, index) => {
                let specData
                let spec
                action.schemaFilter ? specData = { "schema": action.schemaFilter, } : specData = { "schema": action.schema };
                if (fact !== 'empty') {
                    spec = fact2unitSpec(action.filename, specData, fact, action.datares, null, { "width": 477, "height": 268 }, true);
                } else {
                    spec = 'empty';
                }
                if (index === 0) {
                    firstCard = { fact: fact, spec: spec, time: 6000, isGeneratedByAlg:false }
                }
                cards = [...cards, { fact: fact, spec: spec, time: 6000 ,isGeneratedByAlg:false }];
            })
            newState.cardItems = cards;
            newState.currentSpec = firstCard.spec
            return newState;

        case ActionType.REPLAY_VIDEO_SLICE:
            newState.videoIndex = 0
            newState.currentSpec = newState.cardItems[0].spec
            return newState;

        case ActionType.PLAY_VIDEO_SLICE:
            newState.videoIndex = action.index
            newState.currentSpec = newState.cardItems[action.index].spec
            return newState;

        case ActionType.PLAY_VIDEO_CONTINUE:
            newState.isPlayVideo = true;
            return newState;

        case ActionType.ADD_NOW_VIDEO_TIME:
            newState.nowVideoTime = newState.nowVideoTime + 5;
            return newState;

        case ActionType.CHANGE_NOW_VIDEO_TIME:
            newState.nowVideoTime = action.time;
            return newState;
        
        case ActionType.CHANGE_ALL_VIDEO_TIME:
            newState.allVideoTime = action.time;
            return newState;

        case ActionType.STOP_PLAY_VIDEO_CONTINUE:
            newState.isPlayVideo = false;
            return newState;

        case ActionType.ADD_EMPTY_CARD:
            newState.cardItems = [...newState.cardItems, { fact: 'empty', spec: 'empty', time: 6000,isGeneratedByAlg:false }]
            newState.barWidthList = [...newState.barWidthList,{leftbar: 140, rightbar: 18,isDrag:false }]
            newState.allVideoTime = newState.allVideoTime + 6000;
            return newState;

        //delete card 
        case ActionType.REMOVE_CARD:
            if (newState.cardItems.length > 0) {
                newState.allVideoTime = newState.allVideoTime - newState.cardItems[action.index].time;
                newState.cardItems = newState.cardItems.filter((_, i) => i !== action.index)
            }
            return newState;

        //add card
        case ActionType.ADD_CARD:
            let insertFact = _.cloneDeep(action.fact);
            let cardIndex = action.insert;
            let cardDataAll = _.cloneDeep(newState.cardItems);
            //let newUnusedFacts = state.story.unusedFacts.slice();
            //let newFact = newUnusedFacts.splice(factIndex, 1)[0];
            let factToCard = {
                fact: insertFact,
                spec: action.specs,
                time: action.time,
                isGeneratedByAlg:action.isGeneratedByAlg,
            }
            cardDataAll.splice(cardIndex, 0, factToCard);
            newState.cardItems = cardDataAll;
            //newState.story.unusedFacts = newUnusedFacts;
            console.log(newState)
            return newState;
        case ActionType.ADD_CARDS:
            let insertCards = _.cloneDeep(action.cards);
            let cardsIndex = action.insert;
            let cardData = _.cloneDeep(newState.cardItems); 
            for(let x = 0;x<insertCards.length;x++){
                cardData.splice(cardsIndex+x,0,insertCards[x])
            }
            newState.cardItems = cardData;
            return newState;

        //drag card
        case ActionType.CHANGE_CARD:
            let cardDatas = _.cloneDeep(newState.cardItems);
            let origin = action.origin;
            let target = action.target;
            //
            //
            if (origin > target) {
                cardDatas.splice(target, 0, cardDatas[origin]);
                cardDatas.splice(origin + 1, 1)
            }
            else {
                cardDatas.splice(target + 1, 0, cardDatas[origin]);
                cardDatas.splice(origin, 1)
            }
            newState.cardItems = cardDatas;
            console.log(newState)
            return newState;

        //exchange card
        case ActionType.EXCHANGE_CARD:
            let cardAll = _.cloneDeep(newState.cardItems);
            let index = action.index;
            let card = action.card;
            cardAll.splice(index,1,card)
            newState.cardItems = cardAll;
            console.log(newState)
            return newState;

        case ActionType.CHANGE_CARD_FLAG:
            cardDatas = _.cloneDeep(newState.cardItems);
            cardDatas[action.index].isGeneratedByAlg = action.flag;
            newState.cardItems = cardDatas;
            return newState;
            
        // set video leftsider content
        case ActionType.SET_LEFTSIDER_CONTENT:
            newState.leftContent = action.content
            return newState;

        // -------time-line bar-drag
        // init barWidthList
        case ActionType.INIT_TIME_LINE_BAR_WDITH:
            let bar = []
            for (let i = 0; i < action.length; i++) {
                bar = [...bar, { leftbar: 140, rightbar: 18,isDrag:false }]
            }
            newState.barWidthList = bar
            return newState;
        // add bar to barWidthList
        case ActionType.ADD_BAR_TO_TIME_LINE_BAR:
            let addBar = action.bar;
            addBar = {leftbar: 140, rightbar: 18,isDrag:false };
            let addIndex = action.index;
            let newBar = _.cloneDeep(newState.barWidthList);
            newBar.splice(addIndex,0,addBar);
            newState.barWidthList = newBar;
            newState.allVideoTime = newState.allVideoTime + 6000;
            return newState;

        case ActionType.ADD_BARS_TO_TIME_LINE_BAR:
            let addBars = action.bars;
            let myAddIndex = action.index;
            let myNewBar = _.cloneDeep(newState.barWidthList);
            for(let y =0; y<addBars.length;y++){
                myNewBar.splice(myAddIndex+y,0,addBars[y])
            }
            newState.barWidthList = myNewBar;
            newState.allVideoTime = newState.allVideoTime + 6000*addBars.length;
            return newState;
            
        // delete bar from barWidthList
        case ActionType.DELETE_BAR_FROM_TIME_LINE_BAR:
            let deleteIndex = action.index;
            let deletedBar = _.cloneDeep(newState.barWidthList);
            deletedBar.splice(deleteIndex,1);
            newState.barWidthList = deletedBar;
            return newState;

        // exchange bar in barWidthList
        case ActionType.CHANGE_TIME_LINE_BAR:
            let changedBar = _.cloneDeep(newState.barWidthList);
            let originBar = action.origin;
            let targetBar = action.target; 
            if (originBar > targetBar) {
                changedBar.splice(targetBar, 0, changedBar[originBar]);
                changedBar.splice(originBar + 1, 1)
            }
            else {
                changedBar.splice(targetBar + 1, 0, changedBar[originBar]);
                changedBar.splice(originBar, 1)
            }
            newState.barWidthList = changedBar;
            return newState;

        // set barWidth 's left bar chart with index
        case ActionType.SET_LEFT_BAR_WIDTH:
            let leftBarWidth = _.cloneDeep(newState.barWidthList)
            leftBarWidth[action.index].leftbar = action.width
            newState.barWidthList = leftBarWidth
            //change time
            let cardTimes = _.cloneDeep(newState.cardItems);
            let formerTime = cardTimes[action.index].time
            cardTimes[action.index].time=Math.floor(action.width*42.8571429);
            newState.cardItems=cardTimes
            newState.allVideoTime = newState.allVideoTime + (cardTimes[action.index].time - formerTime);
            return newState;

        // set barWidth 's right bar chart with index
        case ActionType.SET_RIGHT_BAR_WIDTH:
            let rightBarWidth = _.cloneDeep(newState.barWidthList)
            rightBarWidth[action.index].rightbar = action.width
            newState.barWidthList = rightBarWidth
            return newState;

        // set drag direction('left','right')
        case ActionType.SET_DRAG_DIRECTION:
            newState.dragDirection = action.direction
            return newState;

        // set mouse down state(true,false)
        case ActionType.SET_MOUSE_DOWN_STATE:
            newState.isMouseDown = action.mouseDown
            return newState;

        // set drag state(true,false)
        case ActionType.SET_DRAG_STATE:
            let barDrag = _.cloneDeep(newState.barWidthList)
            barDrag[action.index].isDrag = action.drag
            newState.barWidthList = barDrag
            return newState;

        case ActionType.SET_CURRENT_X:
            newState.currentX = action.clientx
            return newState;

        case ActionType.SET_LEFT_OFFSET_WIDTH:
            newState.leftOffsetWidth = action.offset
            return newState;

        case ActionType.SET_RIGHT_OFFSET_WIDTH:
            newState.rightOffsetWidth = action.offset
            return newState;

        case ActionType.SET_CURRENT_BAR_INDEX:
            newState.currentBarIndex = action.index
            return newState;

        case ActionType.SET_POSITION:
            newState.scenePosition = action.position
            return newState;

        case ActionType.SET_SCENE_SCALE:
            newState.sceneScale = action.scale
            return newState;
            
        case ActionType.SET_DRAG_CURSOR:
            newState.dragCursor=action.cursor
            return newState;

        case ActionType.QUESTION_2_GIF:
            newState.question=action.question
            newState.narrative_spec = action.narrative_spec
            return newState;

        case ActionType.ISGENERATINGGIF:
            newState.isGenerating = action.isGenerating
            newState.narrative_spec = ''
            return newState;

        case ActionType.CHANGE_TEMP_SPEC:
            newState.temp_spec = action.temp_spec
            return newState;

        case ActionType.OPERATOR_CHANGE:
            newState.operatorType = action.operatorType
            return newState;

        case ActionType.ANIMATION_CHANGE:
            newState.animationType = action.animationType
            return newState;
            
            
        case ActionType.CHANGE_KEYS:
            newState.keys = action.keys
            return newState;

        case ActionType.OPERATOR_ADDING:
            newState.operatorAdds = action.operatorAdds
            return newState;

        case ActionType.CHANGE_OPERATOR:
            let operatorList = _.cloneDeep(newState.narrative_spec.operators);
            let myOrigin = action.origin;
            let myTarget = action.target;
            if (myOrigin > myTarget) {
                operatorList.splice(myTarget, 0, operatorList[myOrigin]);
                operatorList.splice(myOrigin + 1, 1)
            }
            else {
                operatorList.splice(myTarget + 1, 0, operatorList[myOrigin]);
                operatorList.splice(myOrigin, 1)
            }
            newState.narrative_spec.operators = operatorList;
            console.log(newState)
            return newState;

        default:
            break;
    }
    return newState;
}