import Action from './action';
import TitleType from '../visualization/titletype';
import {Fade} from './title'

class AddTitle extends Action {
    constructor(spec) {
        super(spec);
        this._animation = spec.style.animation;
        if ('style' in spec) {
            this._style = spec.style
            if (!this._style.delay)
                this._style.delay = 0;

            if (!this._style.duration)
                this._style.duration = -1;
        } else {
            this._style = { "delay": 0, "duration": 0 }
        }
    }

    operate(vis) {
        let titler = this._type2title(this._animation)
        let chart = vis.chart();
        let style = this._style;
        titler.maketitle(chart, style);
    }

    _type2title(type) {
        switch (type) {
            case TitleType.FADE:
                return new Fade()
            default:
                return new Fade();
        }
    }
}

export default AddTitle;