import Action from './action';

class RemoveEncoding extends Action {
    constructor(spec) {
        super(spec);
        this.channel = spec.channel;
        if ('style' in spec) { this.style = spec.style }
        else {
            this.style = { "delay": 0, "duration": 0 }
        }
    }

    operate(vis) {
        vis.chart().removeEncoding(this.channel, this.style);
    }
}

export default RemoveEncoding;