import Action from './action'
import {Fade} from './caption'
import CaptionType from '../visualization/captiontype'

class AddCaption extends Action {
    constructor(spec) {
        super(spec);
        this._animation = spec.style.animation;
        if ('style' in spec) {
            this._style = spec.style
            if (!this._style.delay)
                this._style.delay = 0;

            if (!this._style.duration)
                this._style.duration = -1;
        } else {
            this._style = { "delay": 0, "duration": 0 }
        }
    }

    operate(vis) {
        let Captioner = this._type2caption(this._animation)
        let chart = vis.chart();
        let style = this._style;
        Captioner.makecaption(chart, style);
    }

    _type2caption(type) {
        switch (type) {
            case CaptionType.FADE:
                return new Fade()
            default:
                return new Fade();
        }
    }
}

export default AddCaption;