import React from 'react';
import { connect } from 'react-redux'
import * as userAction from './action/userAction'
import LazyLoading from '@/components/LazyLoadingView/index'


import './App.less';
import './common.less';
import intl from 'react-intl-universal';
import {
  HashRouter as Router,
  Switch,
  Route,
  // useParams
} from "react-router-dom";
// common locale data
require('intl/locale-data/jsonp/en.js');
require('intl/locale-data/jsonp/zh.js');

// app locale data
const locales = {
  "en-US": require('./locales/en-US.json'),
  "zh-CN": require('./locales/zh-CN.json'),
};


const mapDispatchToProps = dispatch => {
  return {
    updateLocale: (currentLocale) => dispatch(userAction.updateLocale(currentLocale)),
  }
}
const mapStateToProps = (state) => ({
})

class App extends React.Component {

  state = {
    initDone: false,
    currentLocale: ''
  }

  loadLocales(lang) {
    // init method will load CLDR locale data according to currentLocale
    // react-intl-universal is singleton, so you should init it only once in your app
    intl.init({
      currentLocale: lang,
      locales,
    })
      .then(() => {
        this.props.updateLocale(lang)
        // After loading CLDR locale data, start to render
        this.setState(
          { initDone: true, currentLocale: lang });
      });
  }

  initDefaultLang = () => {
    let userDefaultLang = navigator.language || navigator.userLanguage;//IE
    let lang = userDefaultLang.substr(0, 2);//lang2
    if (lang === 'zh') {
      lang = "zh-CN"
    } else {
      lang = "en-US"
    }
    if (this.state.currentLocale !== lang) {
      this.loadLocales(lang)
    }
  }

  componentDidMount() {
    this.initDefaultLang()
  }
  render() {
    const { initDone, currentLocale } = this.state


    const MyRoute = ({ MyComponent, ...rest }) => {
      return (
        <Route
          {...rest}
          render={routerProps => (
            <MyComponent
              intl={intl}
              initDone={initDone}
              currentLocale={currentLocale}
              onChangeLocaleListener={lang => this.loadLocales(lang)}
              {...routerProps} ></MyComponent>)
          } />
      )
    }

    return (
      <Router>
        <Switch>
          <MyRoute exact path='/' MyComponent={LazyLoading("Homepage/index")} />
          {/* <MyRoute exact path='/edit/factsheet' MyComponent={LazyLoading('EditPage/EditFactsheet/index')} /> */}
          <MyRoute exact path='/edit/datavideos' MyComponent={LazyLoading("EditPage/EditDataVideos/index")} />
          {/* <MyRoute exact path='/edit/datacomics' MyComponent={LazyLoading("EditPage/EditDataComics/index")} /> */}
          {/* <MyRoute exact path='/edit/dashbooard' MyComponent={LazyLoading("EditPage/EditDashboard/index")} /> */}
          {/* <MyRoute exact path='/H5/:id' MyComponent={LazyLoading("H5page/index")} /> */}
        </Switch>
      </Router>
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App)