import AutoVis from 'calliope-chart/lib/autovis';
import { Fade } from 'calliope-chart/lib/transitions';

export default class AutoAni {
    constructor() {
        if (!AutoAni.instance) {
            this._container = document.createElement("div");
            this._paragraph = document.createElement("p");
            this._spec = {};
            this._timeouts = [];
            this.autovis = new AutoVis();
            AutoAni.instance = this;
        } else {
            return AutoAni.instance;
        }
    }

    container(value) {
        if (!value) {
            return this._container;
        }
        this._container = value;
    }

    paragraph(value) {
        if (!value) {
            return this._paragraph;
        }
        this._paragraph = value;
    }

    load(spec) {
        this._spec = spec;
    }

    play() {
        let animationDelay = 0;
        let pauseDuration = 1500;
        let transitionDuration = 500;
        let fade = new Fade();
        fade.container(this.container());
        fade.duration(transitionDuration);
        let videoSpec = this._spec;
        let dataspec = videoSpec.data;
        let specs = videoSpec.specs;
        for (const spec of specs) {
            spec.data = dataspec;
            let newspec = JSON.parse(JSON.stringify(spec)) // deepclone
                // if (newspec.chart.duration === 0) {
                //     newspec.chart.duration = 4000; // default duration
                // }

            // play chart animation
            this._timeouts.push(setTimeout(function() {
                this.autovis.container(this.container());
                this.autovis.paragraph(this.paragraph());
                this.autovis.load(newspec);
                this.autovis.shouldShowCaption(true)
                this.autovis.generate();
            }.bind(this), animationDelay));
            animationDelay += 4000;

            // pause
            animationDelay += pauseDuration

            // play transition
            this._timeouts.push(setTimeout(function() {
                fade.animateTransition();
            }, animationDelay));
            animationDelay += transitionDuration;

        }
    }

    stop() {
        for (var i = 0; i < this._timeouts.length; i++) {
            clearTimeout(this._timeouts[i]);
        }
    }
}